.cardlist {
    width: 15vw;
    height: 45vw;
    margin: 0 auto;
    display: block;
    overflow: scroll;
    overflow-x: hidden;
    float:left;
}

.body {
    float: right;
    margin: auto;
}

@media only screen and (max-width: 815px) {
    .cardlist {
        width: 85vw;
        margin: 0 auto;
        display: block;
    }
}

.card-container:hover {
    transform: scale(0.95);
}